const mongoUrl = () => {
	const mongodbUser = process.env.MONGO_USER;
	const mongodbPWd = process.env.MONGO_PWD;
	const mongodbDataBase = process.env.DB_NAME;
	const mongodbConnection = `${process.env.MONGO_URL}:${process.env.MONGO_PORT}`;
	console.log(`mongodb://${mongodbUser}:${mongodbPWd}@${mongodbConnection}/${mongodbDataBase}`);
	return `mongodb://${mongodbUser}:${mongodbPWd}@${mongodbConnection}/${mongodbDataBase}`;
};

export default {
	Api_port: 3000, // Your Free Port For this server
	DB_URL: mongoUrl()
}
